          // 從api取得所有產品資料，加入data的products
          this.products = res.data.products
          this.isLoading = false
          console.log(this.products)
        })
        .catch((err) => {
          alert(err.data.message)
        })
    }
  },
  mounted () {
    this.getProducts()
  }
}
